@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Mono:wght@300&display=swap);
nav {
  display: flex;
  background-color: var(--background);
  color: var(--text);
  justify-content: space-between;
  align-items: stretch;
  grid-gap: 2rem;
  gap: 2rem;
  padding: 0 1rem;
}

.site-title {
  font-size: 2rem;
}

nav ul {
  display: flex;
  list-style: none;
  grid-gap: 1rem;
  gap: 1rem;
  margin: 0;
}

nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0.35rem;
}

nav li.active {
  background-color: var(--primary);
}

nav li:hover {
  background-color: var(--secondary);
}

:root {
  --background: #667292;
  --text: #daebe8;
  --primary: #87bdd8;
  --secondary: #bccad6;
}

* {
  box-sizing: border-box;
  font-family: "Noto Sans Mono", monospace;
}

body {
  margin: 0;
}

.container {
  margin: 1rem;
  text-align: center;
}

.logo {
  width: 50px;
  height: 50px;
  margin: 0.5rem;
}

/* HOME */
.home-container {
  flex-direction: column;
}

.home-container img {
  width: 250px;
  margin: auto;
}

/* CARDS */

.card-container {
  display: flex;
  justify-content: space-around;
  margin-bottom: 3rem;
  padding: 0 6rem;
  grid-gap: 1.5rem 3rem;
  gap: 1.5rem 3rem;
}

.image-text {
  text-align: center;
  font-size: 0.75rem;
}

.card-text {
  max-width: 10rem;
  text-align: left;
  font-size: 0.75rem;
}

/* BADGES */

.badge-card {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  flex-basis: 100%;
  padding: 0 3rem;
}

.badge img {
  width: 150px;
  height: 150px;
}

/* SKILLS */

.skills-container {
  flex-wrap: wrap;
}

.skill-group {
  display: flex;
}

/* PROJECTS */

.project-card {
  max-width: 20rem;
}

